* {
    margin: 0px;
    padding: 0px;
    font-family: Arial, Helvetica, sans-serif;
}
body{
  background-color: gray;
}
#sidebar {
    float: left;
    background-color:#242322;
    width: 10%;
    height: 100%;
}
.button {
  user-select: none;
  float: left;
    border: 1px solid black;
    background: #3d3d3d;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 16px;
    line-height: 18px;
    white-space: nowrap;
    text-decoration: none;
    width: 100%;
    margin: 10px 0;
    cursor: pointer;
  }
  .button:hover{
    background-color: darkgray;
    opacity: 0.8;
  }
  .floatRight{
    float: right;

  }
  .clearFloat{
    clear: both;
  }
  a {
   text-decoration: none; 
    color: white;
  }
  #contents{
    word-wrap: break-word;
    float: left;
    width: 40%;
    height: 100%;
    background-color: violet;
  }
  .contentText{
    margin: 50px 10px;
    font-size: 20px;
  }
  #splash {
    user-select: none;
    float: left;
    margin: 0 2px;
    z-index: 10;
    position: absolute;
    background: rgba(0, 0, 0, 0.5);
    width: 1000px;
    padding: 100px 0;
    text-align: center;
}

#play-btn {
    width: 150px;
    height: 40px;
    background-color: crimson;
    border: 0px;
}

#title {
    color: white;
    font-size: 60px;
    font-family: Arial, Helvetica, sans-serif;
}

#controls {
    color: #c9c9c9;
    font-family: Arial, Helvetica, sans-serif;
    padding-top: 40px;
}

.controlBtn {
    font-weight: bold;
    color: #f0f0f0;
}

#gameover {
  float: left;
  margin-left: 50px;
  width: 1000px;
  height: 580px;
    display: none;
    color: white;
    position: absolute;
    background: rgba(0, 0, 0, 0.5);
    width: 1000px;
    text-align: center;
    font-size: 60px;
    font-family: Arial, Helvetica, sans-serif;
}
#tip{
    user-select: none;
    font-size: 20px;
    font-family: Arial, Helvetica, sans-serif;
}
#game{
  margin-top: 30px;
  float: left;
}
#gamediv{
  margin-top: 12px;
  margin-left: 30px;
  float: left;
}
#log{
  float: left;
}
#gameoverText{
  user-select: none;
  margin-top: 200px;
}
#splash{
  margin-top: 100px;
  margin-left: 50px;
}
#textcontent1{
  float: left;
  height: 100%;
  width: 40%;
  word-wrap: break-word;
  background-color: gray;
}
#homePageImg{
  float: left;
margin: 50px 60px;
}
#gifholder{
  float: left;
  width: 50%;
  height: 100%;
  background-color:black;
}
#gifholder iframe{
  border: 0 solid;
  height: 95%;
  width: 95%;
  -webkit-transform:scale(0.9);
}
#text1{
  color: whitesmoke;
  font-size: 18px;
  font-weight: 500;
  width: 80%;
  margin: 95px auto;
  text-align: justify;
}
h1{
  font-size: 25px;
}
p {
  font-family: monospace;
}
#infoPost{
  color: whitesmoke;
  font-size: 10px;
  width: 80%;
  margin: 0 auto;
}
h2{
  color: #242322;
  font-weight: bold;
  font-family: monospace;
}
.liftedNum{
  text-shadow: 1px 1px 0 #002363;
  font-size: 20px;
  color: black;
  font-weight: bold;
  font-family: sans-serif;
}
